
.counter{position:absolute;position:fixed;z-index:100000;width:150px;height:50px;top:37px;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;-ms-flex-align:center;align-items:center;left:calc(50% - 75px)
}
.counter .page{font-family:'Ziamimi-Bold';font-size:20pt;color:#ffffff
}
.counter .page:before,.counter .page:after{content:'';display:inline-block;position:absolute;width:100%;height:100%;opacity:1;left:0;transform:scaleX(0)
}
.counter .page:before{background-color:#000000
}
.counter .page:after{background-color:#FFFFFF
}
.counter .previous svg,.counter .next svg{fill:#ffffff;width:20px
}
.counter .next{transform:rotateY(180deg)
}
@media screen and (max-width: 980px){
.counter{width:90%;top:78vh;left:5%;position:absolute
}
.counter .page{font-size:70px !important
}
.counter .arrow svg{width:50px
}
}



