.about .bottom .resume:after{content:'';position:absolute;background-color:#1E1E1E;width:0%;height:150%;left:0;right:0;top:5px;bottom:0;margin:auto;z-index:-1;transition:all 0.3s cubic-bezier(0.42, 0, 0.58, 1)
