.project .top h2:before,.project .top h2:after{content:attr(data-text);position:absolute;top:0;left:0;width:100%;background:#1E1E1E;clip:rect(0, 0, 0, 0)
}
.project .top h2:after{left:2px;text-shadow:-1px 0 red;animation:example-one-anim-1 2s 1 linear alternate-reverse
}
.project .top h2:before{left:-2px;text-shadow:2px 0 blue;animation:example-one-anim-2 3s 1 linear alternate-reverse
}
.project .top h2:before,.project .top h2:after{top:20vh
}
.project .top .nextContent{position:relative;line-height:1.5;color:#FFF;height:50vh;width:70%;left:0;right:0;margin:auto;margin-top:50px;max-width:700px;text-align:left;padding:0px 25px;color:#1E1E1E;font-family:'Scout-light';font-size:18px
}
.project .top .nextContent .description{text-align:justify
}
.project .top .nextContent .item{position:relative;margin-top:10px;display:table
}
.project .top .nextContent .item .category{font-weight:bolder;display:block;opacity:0.5;text-transform:uppercase;letter-spacing:6px;margin-bottom:4px;font-size:12px
}
.project .top .nextContent .item:before,.project .top .nextContent .item:after{content:'';display:inline;position:absolute;left:0;top:0;width:100%;height:100%
}
.project .top .nextContent .item.visible{animation:titleAppear 0.1s forwards;animation-delay:2.4s
}
.project .top .nextContent .item.visible:before,.project .top .nextContent .item.visible:after{content:'';display:inline;position:absolute;left:0;top:0;width:100%;height:100%;transform:scaleX(0);animation:beforeAppear 0.5s forwards;animation-delay:2s
}
.project .top .nextContent .item.visible:before{background-color:#1E1E1E
}
.project .top .nextContent .item.visible:after{background-color:#ffffff;animation-delay:2.2s
}
.project .top .nextContent .subContent{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;margin-top:30px
}
.project .top .link{color:#FFF;font-family:'Ziamimi-Bold';position:absolute;left:0;right:0;margin:auto;width:220px;height:50px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;bottom:80px;text-decoration:none;opacity:0
}
.project .top.scrolled{position:fixed !important;height:100vh !important;width:50% !important;top:0 !important
}
.project .top.scrolled .subpart{position:absolute;top:0
}
.project .top.scrolled .subpart .link{opacity:1;animation:linkAppear 5s forwards
}
.project .bottom{overflow:hidden;position:relative;background-color:#1E1E1E;transform:scaleX(1);transform-origin:100% 0%;transition:transform 0.5s
}
.project .bottom .arrowDown{opacity:0;display:block;position:absolute;z-index:100;left:50%;top:95%;transform-origin:50% 50%;transform:translate3d(-50%, -50%, 0);animation:arrow-movement 2s ease-in-out infinite
}
.project .bottom .arrowDown:before,.project .bottom .arrowDown:after{background:#fff;content:'';display:block;height:3px;position:absolute;top:0;left:0;width:30px
}
.project .bottom .arrowDown:before{transform:rotate(-45deg) translateX(23%);transform-origin:top right
}
.project .bottom .arrowDown:after{transform:rotate(45deg) translateX(-23%);transform-origin:top left
}
.project .bottom .subbottom{position:relative
}
.project .bottom .subbottom *{transition:transform 0.5s
}
.project .bottom .subbottom svg{fill:#ffffff;stroke:#ffffff
}
.project .bottom .subbottom .logo{height:45vh;margin-top:25px;padding-top:25px;max-width:50vw;left:0;right:0;margin:auto
}
.project .bottom .subbottom .logo img{height:80%;position:relative
}
.project .bottom .subbottom .shots__item{margin-top:100px;position:relative
}
.project .bottom .subbottom .shots__item img{width:80%;opacity:0
}
.project .bottom .subbottom .shots__item img:before{content:'';width:100%;height:100%;position:absolute;display:block;left:0;top:0;background-color:#000;opacity:0.5
}
.project .bottom.scrolled{animation:bottom-project 1s;animation-iteration-count:1;animation-fill-mode:forwards;transform:scaleX(0.5);will-change:transform
}
.project .bottom.scrolled .arrowDown{display:none
}
.project .bottom.scrolled .subbottom{transform:scaleX(2);position:relative;left:0;right:0;width:50%;margin:auto
}
.project .bottom.scrolled .subbottom h2{position:absolute;top:0
}
.project .bottom.scrolled .subbottom .nextProject{font-family:'Ziamimi-Bold';color:#FFFFFF;margin:80px 0px;width:220px;height:50px;position:relative;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;position:relative;left:calc(50% - 110px)
}
.project .subpart{width:100%;height:100%;position:relative
}
.project .subbottom,.project .subtop{width:100%;height:100%;left:0%;position:relative
}
.project .subbottom:before,.project .subbottom:after,.project .subtop:before,.project .subtop:after{content:'';display:inline-block;position:absolute;width:100%;height:100%;opacity:1;top:0;left:0;transform:scaleX(0)
}
.project .subbottom:before,.project .subtop:before{background-color:#000000
}
.project .subbottom:after,.project .subtop:after{background-color:#FFFFFF
}
.nextProject svg,.link svg{width:100%;height:100%;position:absolute;left:0
}
.nextProject svg line,.link svg line{stroke-width:4px;stroke:#FFF;fill:none;transition:all 0.5s cubic-bezier(0.42, 0, 0.58, 1)
}
.nextProject svg line.up,.nextProject svg line.down,.link svg line.up,.link svg line.down{stroke-dasharray:220
}
.nextProject svg line.left,.nextProject svg line.right,.link svg line.left,.link svg line.right{stroke-dasharray:50
}
.nextProject svg:hover line.up,.link svg:hover line.up{transform:translate(-440px)
}
.nextProject svg:hover line.down,.link svg:hover line.down{transform:translate(440px)
}
.nextProject svg:hover line.left,.link svg:hover line.left{transform:translateY(-100px)
}
.nextProject svg:hover line.right,.link svg:hover line.right{transform:translateY(100px)
}
@keyframes bottom-project{
0%{height:50%
}
50%{height:50%
}
100%{height:auto
}
}
@keyframes beforeAppear{
0%{transform:scaleX(0);transform-origin:0% 0%
}
50%{transform:scaleX(1);transform-origin:0% 0%
}
51%{transform:scaleX(1);transform-origin:100% 0%
}
100%{transform:scaleX(0);transform-origin:100% 0%
}
}
@keyframes titleAppear{
0%{color:#1E1E1E
}
100%{color:#FFFFFF
}
}
@keyframes beforeLeave{
0%{transform:scaleX(0);transform-origin:0% 0%
}
100%{transform:scaleX(1);transform-origin:0% 0%
}
}
@keyframes linkAppear{
0%{opacity:0
}
50%{opacity:0
}
60%{opacity:1
}
}
@keyframes text-glitch{
0%{transform:translate(2px, 5px)
}
1%{transform:scaleY(1) skewX(0deg)
}
1.5%{transform:scaleY(3) skewX(-60deg)
}
2%{transform:scaleY(1) skewX(0deg)
}
51%{transform:scaleX(1) scaleY(1) skewX(0deg)
}
52%{transform:scaleX(1.5) scaleY(0.2) skewX(80deg)
}
53%{transform:scaleX(1) scaleY(1) skewX(0deg)
}
}
@keyframes arrow-movement{
0%{opacity:0;top:90%
}
70%{opacity:1
}
100%{opacity:0
}
}
@media screen and (max-width: 980px){
.project .top{height:auto !important;position:relative !important
}
.project .top .link{animation:linkAppear 5s forwards;opacity:1
}
.project .top .nextContent{width:90% !important;max-width:2000px !important
}
.project .top .nextContent .item{font-size:30px !important
}
.project .top .nextContent .item .category{font-size:20px
}
.project h2{width:90%;line-height:50px
}
.project .logo{height:100% !important;max-width:100vw !important;transform:scale(0.8)
}
.project .logo img{width:100%
}
.project .bottom.scrolled{width:100vw !important;animation-name:none !important;height:auto !important;transform:scale(1) !important;position:relative !important;top:0 !important
}
.project .bottom.scrolled .subbottom{transform:scale(1) !important;width:100%;padding-top:100px
}
.project .nextProject,.project .link{border:solid 3px #FFFFFF
}
.project .nextProject svg,.project .link svg{display:none
}
}



