
@font-face{font-family:"Ziamimi-Bold";src:url("assets/fonts/ZiamimiBold.otf")
}
@font-face{font-family:"Ziamimi-Light";src:url("assets/fonts/ZiamimiLight.otf")
}
@font-face{font-family:"Ziamimi-Bold";src:url("assets/fonts/ZiamimiRegular.otf")
}
@font-face{font-family:'Scout-light';src:url("assets/fonts/Scout-Light.otf")
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#000;height:100%;width:100%
}
#app:before{content:'';position:fixed;width:300%;height:300%;background-image:url("assets/images/background-noise.png");z-index:20;top:-100%;left:-100%;pointer-events:none;animation:grain 1s steps(10) infinite
}
#app:nth-child(1){position:absolute;background-color:transparent;width:100%
}
#app .loader{position:absolute;left:50%;top:50%;z-index:1000;margin-left:-27.5px;margin-top:-27.5px
}
#app .loader:before{content:'';display:inline;width:100vw;height:100vh;left:0;top:0;position:fixed;background-color:#1E1E1E
}
#app .square{background:white;width:15px;height:15px;float:left;top:-100px;margin-right:5px;margin-top:5px;position:relative;opacity:0;animation:enter 3s infinite
}
#app .enter{top:0px;opacity:1
}
#app .square:nth-child(1){animation-delay:.6s
}
#app .square:nth-child(2){animation-delay:.7s
}
#app .square:nth-child(3){animation-delay:.8s;background:#fdc96f
}
#app .square:nth-child(4){animation-delay:.3s
}
#app .square:nth-child(5){animation-delay:.4s
}
#app .square:nth-child(6){animation-delay:.5s
}
#app .square:nth-child(8){animation-delay:.1s
}
#app .square:nth-child(9){animation-delay:.2s
}
#app .clear{clear:both
}
#app .last{margin-right:0
}
#app .nav{position:fixed;top:50px;width:40vw;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around
}
#app .nav .aboutLink,#app .nav .projectLink,#app .nav .homeLink{position:relative;font-family:'Ziamimi-Bold';color:#ffffff;font-size:14px;text-decoration:none;opacity:0.8
}
#app .nav .aboutLink.router-link-active,#app .nav .projectLink.router-link-active,#app .nav .homeLink.router-link-active{opacity:1
}
#app .nav .aboutLink.router-link-active:after,#app .nav .projectLink.router-link-active:after,#app .nav .homeLink.router-link-active:after{transform:scaleX(1) !important
}
#app .nav .aboutLink:after,#app .nav .projectLink:after,#app .nav .homeLink:after{content:'';position:absolute;height:2px;width:100%;background-color:#ffffff;left:0;bottom:-5px;transition:all 0.2s ease-out;transform-origin:50% 50%;transform:scaleX(0)
}
#app .nav .aboutLink:hover:after,#app .nav .projectLink:hover:after,#app .nav .homeLink:hover:after{transform:scaleX(1)
}
#app .nav .aboutLink:hover,#app .nav .projectLink:hover,#app .nav .homeLink:hover{opacity:1
}
#app .nav .homeLink ul{width:10px;position:relative;top:-10px
}
#app .nav .homeLink ul li{width:20px;background-color:#FFFFFF;height:3px;list-style:none;position:absolute;left:calc(50% - 20px);top:0;transform:scaleX(1);transition:all 0.5s
}
#app .nav .homeLink ul li:nth-child(1){transform:rotateZ(45deg) scaleX(1)
}
#app .nav .homeLink ul li:nth-child(2){transform:rotateZ(-45deg) scaleX(1)
}
#app .nav .homeLink:after{background-color:transparent
}
#app .nav .homeLink.router-link-active ul li:nth-child(1){transform:rotateZ(45deg) scaleX(0.1)
}
#app .nav .homeLink.router-link-active ul li:nth-child(2){transform:rotateZ(-45deg) scaleX(0.1)
}
#app .top{left:0%;position:absolute;width:100%;height:50vh
}
#app .bottom{left:0%;position:absolute;width:100%;top:50vh;height:50vh
}
#app .navigation-enter-active,#app .navigation-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;position:relative;transition-delay:1s
}
#app .navigation-enter-active .top,#app .navigation-leave-active .top{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0;transition-delay:1s
}
#app .navigation-enter-active .bottom,#app .navigation-leave-active .bottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0;transition-delay:1s
}
#app .navigation-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);position:relative;transition-delay:1s
}
#app .navigation-enter .top{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%;transition-delay:1s
}
#app .navigation-enter .bottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%;transition-delay:1s
}
#app .navigation-leave-to{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);position:relative;transition-delay:1s
}
#app .navigation-leave-to .top{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%;transition-delay:1s
}
#app .navigation-leave-to .bottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%;transition-delay:1s
}
#app .subnavigation-enter-active,#app .subnavigation-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;position:absolute
}
#app .subnavigation-enter-active .subtop,#app .subnavigation-leave-active .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0
}
#app .subnavigation-enter-active .subbottom,#app .subnavigation-leave-active .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0
}
#app .subnavigation-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1)
}
#app .subnavigation-enter .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%
}
#app .subnavigation-enter .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%
}
#app .subnavigation-leave-to{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1)
}
#app .subnavigation-leave-to .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%
}
#app .subnavigation-leave-to .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%
}
.subnavigation-enter-active,.subnavigation-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;position:absolute
}
.subnavigation-enter-active .subtop,.subnavigation-leave-active .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0
}
.subnavigation-enter-active .subbottom,.subnavigation-leave-active .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:0
}
.subnavigation-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1)
}
.subnavigation-enter .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%
}
.subnavigation-enter .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%
}
.subnavigation-leave-to{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1)
}
.subnavigation-leave-to .subtop{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:-100%
}
.subnavigation-leave-to .subbottom{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);left:100%
}
.scrollednavigation-enter-active{transition:all 3s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-enter-active .subtop,.scrollednavigation-enter-active .subbottom{transition:all 3s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-enter-active .subtop:before,.scrollednavigation-enter-active .subbottom:before{transition:all 1s;transform:scaleX(0);transform-origin:0% 0%
}
.scrollednavigation-enter-active .subtop:after,.scrollednavigation-enter-active .subbottom:after{transition:all 0.5s;transform:scaleX(0);transform-origin:0% 0%
}
.scrollednavigation-leave-active{transition:all 1s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-leave-active .subtop,.scrollednavigation-leave-active .subbottom{transition:all 1s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-leave-active .subtop:before,.scrollednavigation-leave-active .subbottom:before{transition:all 1s;transform:scaleX(0);transform-origin:100% 100%
}
.scrollednavigation-leave-active .subtop:after,.scrollednavigation-leave-active .subbottom:after{transition:transform 0.5s;transform:scaleX(0);transform-origin:100% 100%
}
.scrollednavigation-enter{transition:all 1s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-enter .subtop,.scrollednavigation-enter .subbottom{transition:all 1s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-enter .subtop:before,.scrollednavigation-enter .subbottom:before{transition:transform 1s;transform:scaleX(1);transform-origin:0% 0%
}
.scrollednavigation-enter .subtop:after,.scrollednavigation-enter .subbottom:after{transition:transform 0.5s;transform:scaleX(1);transform-origin:0% 0%
}
.scrollednavigation-leave-to{transition:all 1s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-leave-to .subtop,.scrollednavigation-leave-to .subbottom{transition:all 1s cubic-bezier(0.42, 0, 0.58, 1)
}
.scrollednavigation-leave-to .subtop:before,.scrollednavigation-leave-to .subbottom:before{transition:transform 1s;transform:scaleX(1);transform-origin:100% 100%
}
.scrollednavigation-leave-to .subtop:after,.scrollednavigation-leave-to .subbottom:after{transition:transform 0.5s;transform:scaleX(1);transform-origin:100% 100%
}
.mobile-enter-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:1;transition-delay:0.5s
}
.mobile-leave-active{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:0;transition-delay:0.5s
}
.mobile-enter{transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);opacity:0;transition-delay:0.5s
}
.mobile-leave{opacity:1;transition:all 0.7s cubic-bezier(0.42, 0, 0.58, 1);transition-delay:0.5s
}
@keyframes enter{
0%{opacity:0;top:-100px
}
5%{opacity:1;top:0px
}
50.9%{opacity:1;top:0px
}
55.9%{opacity:0;top:100px
}
}
@keyframes grain{
0%{transform:translate(0)
}
10%{transform:translate(-5%, -10%)
}
20%{transform:translate(-15%, 5%)
}
30%{transform:translate(7%, -25%)
}
40%{transform:translate(21%, 25%)
}
50%{transform:translate(-25%, 10%)
}
60%{transform:translate(15%, 5%)
}
70%{transform:translateY(15%)
}
90%{transform:translate(-10%, 10%)
}
}
@media screen and (max-width: 980px){
.nav{top:0 !important;width:100vw !important;height:50px !important;padding-top:50px !important;background-color:#1E1E1E
}
.nav a{height:30px;font-size:30px !important
}
}



